.check {
  display: flex;
  width: 32px;
  height: 32px;
  margin: 9px 19px;
  box-sizing: border-box;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  cursor: pointer;
}

.checkDone {
  display: flex;
  width: 32px;
  height: 32px;
  margin: 9px 19px;
  box-sizing: border-box;
  border: 1px solid #4d4d4d;
  border-radius: 50%;
  background-image: url(../image/check.png);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.text {
  font-size: 20px;
  align-self: center;
  color: #4d4d4d;
}

.textDone {
  font-size: 20px;
  align-self: center;
  color: #d9d9d9;
  text-decoration: line-through;
}